@mixin typeContainer($color){
    width: 100px;
    font-size: 20px;
    border-radius: 10px;
    border: solid 2px black;
    text-align: center;
    margin-top: 15px;
    color:$color
}

@mixin typeColor($color,$hover){
    background-color: $color;

    &:hover{
        cursor: pointer;
        background-color: $hover;
    }

    &:disabled{
        cursor: not-allowed;
        background-color: $hover;
        color: #CCCCCC;
    }
}

